import React from 'react';
import booki from "../assets/images/Booki.webp";
import ohMyFood from "../assets/images/ohmyfood.webp";
import laChouette from "../assets/images/lachouette.webp";
import kanap from "../assets/images/kanap.webp";
import piquante from "../assets/images/piquante.webp";
import groupomania from "../assets/images/groupomania3.webp";
import Zoom from 'react-reveal/Zoom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLink } from '@fortawesome/free-solid-svg-icons';



const Portfolio = () => {
    
    return (
            <section className="portfolio">


                <h1 className="heading"> <span>mes</span> projets </h1>

                <div className="box-container">
                <Zoom>
                    <div className="box">
                        <img src={booki} alt="Projet Booki" />
                        <div className="content">
                        <h2>Booki</h2>
                            <p>Reproduction d'un site web via une maquette en HTML et CSS et implémentation d'une interface entierement responsive.</p>

                        </div>
                    </div>

                    <div className="box">
                        <img src={ohMyFood} alt="Projet OhMyFood" />
                        <div className="content">
                        <a href="https://argozen1.github.io/RomainAres_3_06122021./" target="_blank" rel="noreferrer"><h2>OhMyFood <FontAwesomeIcon icon={faLink} /></h2></a>
                            <p>Utilisation de HTML et CSS et du préprocesseur Sass. Conception du site web en mobile first pour les besoins du client, site 100% responsive. Mise en place d'animations poussées avec Sass. Utilisation de git et de github pour le projet.</p>

                        </div>
                    </div>

                    <div className="box">
                        <img className='la-chouette-agence' src={laChouette} alt="Projet La chouette agence" />
                        <div className="content">
                        <a href="https://argozen1.github.io/P4_02_code_source/" target="_blank" rel="noreferrer"><h2>La Chouette agence <FontAwesomeIcon icon={faLink} /></h2></a>
                            <p>Optimisation d'un site web, optimisation du référencement du site web(SEO) Mise en place d'un code HTML et CSS maintenable. Optimisation de la performance du site. Conception d'un contenu web accessible conformément aux règles WCAG.</p>

                        </div>
                    </div>

                    <div className="box">
                        <img className='kanap' src={kanap} alt="Projet Kanap" />
                        <div className="content">
                        <a href="https://github.com/ArgoZen1/P5-Kanap" target="_blank" rel="noreferrer"><h2>Kanap <FontAwesomeIcon icon={faLink} /></h2></a>
                            <p>Utilisation de JavaScript côté frontend en communiquant avec une API externe pour récupérer des données. Utilisation de la méthode fetch pour récupérer les ressources à travers le reseau de manière asynchrone. Envoi des données au backend.</p>

                        </div>
                    </div>

                    <div className="box">
                        <img className='piquante' href="" src={piquante} alt="Projet piquante" />
                        <div className="content">
                        <a href="https://github.com/ArgoZen1/P6_API_project" target="_blank" rel="noreferrer"><h2>Hot Takes <FontAwesomeIcon icon={faLink} /></h2></a>
                            <p>Construction d'une API sécurisée pour une application d'avis gastronomiques. Réalisation d'une API REST avec Node.js Express et MongoDB. Création d'un modéle de sécurité en appliquant les techniques de OWASP. Mise en œuvre des opérations CRUD de manière sécurisée.</p>

                        </div>
                    </div>

                    <div className="box">
                        <img className='groupomania' src={groupomania} alt="Projet Groupomania" />
                        <div className="content">
                            <a href="https://github.com/ArgoZen1/Groupomania" target="_blank" rel="noreferrer"><h2>Groupomania <FontAwesomeIcon icon={faLink} /></h2></a>
                            <p>Réalisation d'un reseau social d'entreprise. Utilisation de React, Redux, Sass, Node.js, Express et MySQL. Authentification d'un utilisateur et maintenant sa session. Personnalisation du contenu envoyé à un client web Gestion d'un stockage de données avec MySQL de manière sécurisée.</p>
                        </div>
                    </div>

</Zoom>
                </div>

            </section>

    );
};

export default Portfolio;