import React from 'react';
import { bounceInDown, bounceInLeft } from 'react-animations';
import Radium, { StyleRoot } from 'radium';
import { useTypewriter, Cursor } from 'react-simple-typewriter';
import About from './About';
import Portfolio from './Portfolio';
import Contact from './Contact';
import test1 from '../assets/images/stack-hero-min.webp';
import Scroll from './Scroll';
import cv from '../assets/images/cv-ares-romain-N1.pdf';



const styles = {
  bounceInDown: {
    animation: 'x 1.5s',
    animationName: Radium.keyframes(bounceInDown, 'bounceInDown')
  }
}
const styles2 = {
  bounceInDown: {
    animation: 'x 1.3s',
    animationName: Radium.keyframes(bounceInDown, 'bounceInDown')
  }
}
const styles3 = {
  bounceInDown: {
    animation: 'x 1.1s',
    animationName: Radium.keyframes(bounceInDown, 'bounceInDown')
  }
}
const styles4 = {
  bounceInDown: {
    animation: 'x 1s',
    animationName: Radium.keyframes(bounceInDown, 'bounceInDown')
  }
}

const styles5 = {
  bounceInLeft: {
    animation: 'x 1.2s',
    animationName: Radium.keyframes(bounceInLeft, 'bounceInLeft')
  }
}


const Home = () => {

  const { text } = useTypewriter({
    words: ['SEO', 'Performance', 'Accessibilité', 'Responsive design', 'OWASP'],
    loop: 0,
  })

  return (
    <>
    
      <StyleRoot>
        <section className="home">
          <div style={styles5.bounceInLeft} className="image">

            <img style={styles5.bounceInLeft} src={test1} alt="" />

          </div>
          <div className="content">

            <h1 style={styles.bounceInDown}>Arès Romain</h1>
            <span className='span' style={styles2.bounceInDown}>Développeur Web</span>
            <p style={styles3.bounceInDown}>Développeur web, et passionné d’informatique.</p>
            <span className="span3">{text}<Cursor /></span>
            <a style={styles4.bounceInDown} href={cv} target="_blank" rel='noreferrer' className="btn"> Mon CV<i className="fa fa-briefcase"></i></a>

          </div>
          <Scroll />

        </section>
        
      </StyleRoot>
      <About />
      <Portfolio />
      <Contact />
     
    </>
  );
};

export default Home;