import React from 'react';
import { useRef } from 'react';
import emailjs from '@emailjs/browser';
import Flip from 'react-reveal/Flip';
import Fade from 'react-reveal/Fade';

const Contact = () => {

    const form = useRef();

    const sendEmail = async (e) => {
        e.preventDefault();

         emailjs.sendForm('service_tmihuwo', 'template_wd65c1f', form.current, 'sM1a-NJ1xHRkv2TkB')


            .then((result) => {
                console.log(result.text);
                e.target.reset()
                window.confirm("Votre message a bien été envoyé")
            }, (error) => {
                console.log(error.text);
            });
    };

    return (

        <section className="contact">

            <h1 className="heading">contactez-<span>moi</span> </h1>

            <div className="row">

                <div className="info-container">

                <Flip left><h1>Entrer en contact</h1></Flip>

                    <p>Si vous voulez me contacter, me parler d'une collaboration de projet ou simplement dire bonjour,
                        remplissez le formulaire.</p>

                    <div className="box-container">

                        <div className="box">
                            <div className="info">
                                <h2>adresse :</h2>
                                <p>France, Concarneau</p>
                            </div>
                        </div>

                        <div className="box">

                            <div className="info">
                                <h2>email :</h2>
                                <p><a className='mail' href="mailto:argozen.dev@gmail.com">argozen.dev@gmail.com</a></p>
                            </div>
                        </div>

                        <div className="box">

                            <div className="info">
                                <h2>LinkedIn :</h2>
                                <p><a className='mail' href="https://www.linkedin.com/in/romain-ares/">Mon profil sur LinkedIn</a></p>
                            </div>
                        </div>


                    </div>



                </div>
                
                <form ref={form} onSubmit={sendEmail}>
                <Fade right>
                    <div className="inputBox">
                        <input type="text" name='name' id='name' placeholder='Votre nom complet' aria-label='Votre nom complet' required />
                        <input type="number" name='number' placeholder='Votre numéro' aria-label='Votre numéro' />
                    </div>

                    <div className="inputBox">
                        <input type="email" name='email' placeholder='Votre email' aria-label='Votre email' required />
                        <input type="text" name='sujet' placeholder="Le sujet" aria-label='Votre sujet' />
                    </div>

                    <textarea name="message" placeholder="Votre message" cols="30" rows="10" aria-label='Votre message' required></textarea>

                    <button type='submit' id="btn2">ENVOYER</button>
</Fade>
                </form>
                

            </div>

        </section>

    );
};

export default Contact;