import { NavLink } from 'react-router-dom';
import React, { useState } from 'react';
import logo from '../../assets/images/new-logo-AR-min-trans.webp'


const NavBar = () => {

    const [click, setClick] = useState(false);
  const [activeNav, setActiveNav] = useState('/')
  const handleClick = () => setClick(!click);

    return (
        <nav className="navbar">
        
        <div className='nav-container'>
        
          <NavLink to="/" onClick={() => setActiveNav('/')} className={activeNav === '/' ? 'nav-logo' : 'nav-logo'}>
            <img src={logo} alt="Mon logo" />
          </NavLink>
          <ul className={click ? "nav-menu active" : "nav-menu"}>
            <li className='nav-item'>
              <NavLink to="/"  onClick={() => setActiveNav('/')} className={activeNav === '/' ? 'nav-links' : 'nav-links'}>
                <span>Accueil</span>
              </NavLink>
            </li>
            <li className='nav-item'>
              <NavLink to="/about" onClick={() => setActiveNav('/skills')} className={activeNav === '/skills' ? 'active' : 'nav-links'}>
                <span>À propos</span>
              </NavLink> 
            </li>
            <li className='nav-item'>
              <NavLink to="/portfolio" onClick={() => setActiveNav('/portfolio')} className={activeNav === '/portfolio' ? 'active' : 'nav-links'}>
                <span>Portfolio</span>
              </NavLink>
            </li>
            <li className='nav-item'>
              <NavLink to="/contact" onClick={() => setActiveNav('/contact')} className={activeNav === '/contact' ? 'active' : 'nav-links'}>
                <span>Contact</span>
              </NavLink>
            </li>
            
          </ul>
          <div className='nav-icon' onClick={handleClick}>
            <i className={click ? "fas fa-times" : "fas fa-bars"}></i>
          </div>
          
        </div>
        
      </nav>
    );
};

export default NavBar;